<template>
    <div class="card mt-5">
        <b-form-group> 
            <template slot="label">
                <b>Seleccione los aos:</b><br />
                <b-form-checkbox
                v-model="allSelectedYears"
                :indeterminate="indeterminate"
                aria-describedby="Years"
                aria-controls="years"
                @change="toggleAllYears"
                >
                {{ allSelectedYears ? 'Quitar Todos' : 'Todos' }}
                </b-form-checkbox>
            </template>

            <b-form-checkbox-group
                id="years"
                stacked
                v-model="selectedYears"
                name="flavs"
                :options="years"
                class="ml-4"
                aria-label="Individual years"
            />
        </b-form-group>
        <div v-show="typeChart === 'Anual' && detail === 'ejecucion'">
            <h4>Graficas anuales, dia de ejecucin</h4>
            <br />
            <div class="col-lg-8" v-if="selectedYears.length > 0">
                Comensales
                <comensalesAnual :years="selectedYears"/>
                Dinero
                <dineroAnual :years="selectedYears"/>
            </div>
        </div>
        <div v-show="typeChart === 'Anual' && detail === 'venta'">
             <h4>Graficas anuales, dia de venta</h4>
            <br />
            <div class="col-lg-8" v-show="selectedYears.length > 0">
                Comensales
                <comensalesAnualVenta :years="selectedYears"/>
                Dinero
                <dineroAnualVenta :years="selectedYears"/>
            </div>
        </div>
        {{typeChart}}
        <div v-show="typeChart === 'Mensual'">
            <b-row>
                <b-col>
                    <b-form-select v-model="selectedType" :options="optionsType"></b-form-select>
                </b-col>
                <b-col>
                    <b-form-select v-if="selectedType == 'evento'" v-model="selectedEvent" :options="optionsEvents"></b-form-select>
                    <b-form-select v-else v-model="selectedRoom" :options="optionsRoom"></b-form-select>
                </b-col>
            </b-row>
            <div class="col-lg-8" v-show="selectedYears.length > 0">
                Comensales
                <comensalesMensualVenta :years="selectedYears" :type="selectedType" :event="selectedEvent" :room="selectedRoom" :detalle="detail"/>
                Dinero
                <dineroMensualVenta :years="selectedYears" :type="selectedType" :event="selectedEvent" :room="selectedRoom" :detalle="detail"/>
            </div>
        </div>
        <div v-show="typeChart === 'Grafica3'">
            <chart1 />
        </div>
    </div>
</template>
<script>
import comensalesAnual from './anual/comensales'
import dineroAnual from './anual/dinero'
import dineroAnualVenta from './anual/dineroVentas'
import comensalesAnualVenta from './anual/comensalesVentas'
import comensalesMensualVenta from './mensual/comensalesVenta'
import dineroMensualVenta from './mensual/dineroVenta'
import chart1 from '../../views/Chart1'
const axios = require('axios');
var _ = require('lodash');
// const moment = require("moment")
export default {
    components: {
        comensalesAnual,
        dineroAnual,
        dineroAnualVenta,
        comensalesAnualVenta,
        comensalesMensualVenta,
        dineroMensualVenta
    },
    data () {
        return {
            selectedYears: [],
            selectedType: null,
            selectedEvent: null,
            allSelectedYears: [],
            allSelectedYearsVentaAnual: [],
            yearsS: [],
            indeterminate: false,
            years: [],
            selectedRoom: null,
            optionsType: [
              { value: null, text: 'Seleccione un tipo de comparacin' },
              { value: 'evento', text: 'Tipo de evento' },
              { value: 'salon', text: 'Salon' }
            ],
            optionsEvents: [
              { value: null, text: 'Seleccione un tipo de evento' }
            ],
            optionsRoom: [
              { value: null, text: 'Seleccione un Saln' }
            ]
        }
    },
    mounted () {
        this.getYears()
        this.getSalones()
        this.getEventType()
    },
    watch: {
        selectedYears (val) {
            this.yearsS = val
        },
        selectedType (val) {
            if (val === 'evento') {
                this.selectedRoom = null
            }
            if (val === 'salon') {
                this.selectedEvent = null
            }
        }
    },
    computed: {
        typeChart () {
            return this.$route.params.grafica
        },
        detail () {
            return this.$route.params.detalle
        }
    },
    methods: {
        getEventType () {
            let payload = this.toFormData({opcion: 'tipo_evento'});
            // eslint-disable-next-line no-console
            axios
            .post('https://villaconin.mx/reportes/mysqli.php', payload)
            .then(response => {
                // eslint-disable-next-line no-console
                console.log(response)
                response.data.forEach(element => {
                    if (element !== 'Presentacion' && element !== 'Cumpleaos') {
                    this.optionsEvents.push({ value: element, text: element})
                    }
                });
            })
            .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
                this.errored = true
            })
            .finally(() => this.loading = false)
        },
        getSalones () {
            let payload2 = this.toFormData({opcion: 'salones'});
            // eslint-disable-next-line no-console
            axios
            .post('https://villaconin.mx/reportes/mysqli.php', payload2)
            .then(response => {

                // eslint-disable-next-line no-console
                response.data.forEach(element => {
                    if (element !== 'Presentacion' && element !== 'Cumpleaos') {
                    this.optionsRoom.push({ value: element, text: element})
                    }
                });
            })
            .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
                this.errored = true
            })
            .finally(() => this.loading = false)
        },
        getYears () {
            let personForm = this.toFormData({opcion: 'yearSistem'});
            axios
            .post('https://villaconin.mx/reportes/mysqli.php', personForm)
            .then(response => {
                _.forEach(response.data, (elementc) => {
                        this.years.push(elementc)
                })
            })
            .catch(error => {
                // eslint-disable-next-line no-console
                console.log(error)
                this.errored = true
            })
            .finally(() => this.loading = false)
        },
        toFormData: function(obj) {
            let formData = new FormData();
            for(let key in obj) {
                formData.append(key, obj[key]);
            }
            return formData;
        },
        toggleAllYears(checked) {
            this.selectedYears = checked ? this.years.slice() : []
            this.allSelectedYearsVentaAnual = checked ? this.years.slice() : []
        }
    }
}
</script>